@import url(https://fonts.googleapis.com/css2?family=Eczar&display=swap);
body {
  margin: 0;
  background: rgb(234,237,237);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.header{
    background: #131921;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header__logo{
    width: 100px;
    margin: 18px 20px 0;
}
a.header__textLogo {
    text-decoration: none;
    color: #fff;
    font-size: 24px;
    line-height: 3rem;
    margin: 0px 20px 0;
    font-family: 'Eczar', serif;

}
.header__search{
    display: flex;
    flex: 1 1;
}
.header__searchInput{
    height: 22px;
    padding: 10px;
    width: 100%;
    border: none;
}
.header__searchIcon{
    background: #cd9042;
    padding: 5px;
    height: 22px!important;
}
.header__link{
    color: #ffffff;
    text-decoration: none;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin: 0 10px;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header__optionBasket{
    display: flex;
    align-items: center;
}
.header__basketCount{
    margin: 0 10px;
}
.home{
    max-width: 1180px;
    margin: 0 auto;
}
.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home__iamge{
    width: 100%;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    z-index: -1;
    margin-bottom: -150px;
}
.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background: #ffffff;
    margin: 10px;
    z-index: 1;
}
.product__rating{
    color: #f0c14b;
    margin-top: 5px;
}
.procut__info{
    margin-bottom: 10px;
}
.product > a{
    text-decoration: none;
    width: 100%;
    height: 100%;
}
.product > a > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button {
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 3px;
}
.checkout{
    display: flex;
    background: white;
    padding: 20px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.checkout__ad{
    width: 100%;
    margin-bottom: 10px;
}
.checkout__description > h2{
    margin-bottom: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.checkoutProduct{
    display: flex;
}
.checkoutProduct_image{
    height: 180px;
    width: 180px;
}
.checkoutProduct__info {
    margin-left: 20px;
}
.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}
.checkoutProduct__rating{
    color: #f0c14b;
    margin-top: 5px;
}
.checkoutProduct__button{
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    padding: 5px 10px;
}
.subtotal{
    background: #f3f3f3;
    border: 1px solid #dddddd;
    padding: 10px;
    display: flex;
    flex-direction: column;
    height: 100px;
    width: 300px;
    justify-content: space-between;
    border-radius: 3px;
}
.subtotal__button{
     background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    padding: 5px 10px;
}
.subtotal__gift{
    display: flex;
    align-items: center;
}
.subtotal__gift > input {
    margin-right: 5px;
}
.productDetails{
    display: flex;
    padding: 0 100px;
}
.productDetails__left{
    width: 50%;
    padding-right: 100px;
}
.productDetails__right{
    width: 50%;
    display: flex;
    flex-direction: column;
    margin: 30px 0 0 30px;
}
.carousel .slide img{
    height: 300px;
}
p.carousel-status{
    display: none;
}
.productDetails__rating{
    color: #f0c14b;
    margin: 10px 0;
}
.productDetails__price{
    margin: 10px 0;
}
.productDetails__info > p{
    font-size: 24px;
    font-weight: 800;
}
.productDetails__button{
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    padding: 5px 10px;
}
.productdetailbottom{
    margin-bottom: 15px;
    padding: 0 20px;
}
.rwt__tabpanel {
    padding: 30px 20px;
}
